'use client';

import { AdminHeader } from '@/components/admin/admin-header';
import { db } from '@/lib/firebase';
import PhotoCamera from '@mui/icons-material/PhotoCamera';
import CircularProgress from '@mui/material/CircularProgress';
import Slider from '@mui/material/Slider';
import { addDoc, collection } from 'firebase/firestore';
import { getDownloadURL, getStorage, ref, uploadBytes } from 'firebase/storage';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useRef, useState } from 'react';
import Cropper from 'react-easy-crop';

function getCroppedImg(imageSrc: string, crop: any, zoom: any, aspect: any) {
  // Helper to crop the image in the browser
  // We'll use a canvas to crop and resize to 400x400
  return new Promise((resolve, reject) => {
    const image = new window.Image();
    image.crossOrigin = 'anonymous';
    image.src = imageSrc;
    image.onload = () => {
      const canvas = document.createElement('canvas');
      canvas.width = 400;
      canvas.height = 400;
      const ctx = canvas.getContext('2d');
      if (!ctx) return reject(new Error('Canvas context is null'));
      const scale = image.width / image.naturalWidth;
      const cropX = crop.x * scale;
      const cropY = crop.y * scale;
      ctx.drawImage(
        image,
        cropX,
        cropY,
        crop.width * scale,
        crop.height * scale,
        0,
        0,
        400,
        400
      );
      canvas.toBlob(blob => {
        if (!blob) {
          reject(new Error('Canvas is empty'));
          return;
        }
        resolve(blob);
      }, 'image/jpeg', 0.95);
    };
    image.onerror = error => reject(error);
  });
}

export default function AddMerchandise() {
  const [form, setForm] = useState({
    name: '',
    description: '',
    price: 0,
    enabled: true,
    imageUrl: '',
  });
  const [saving, setSaving] = useState(false);
  const [imageSrc, setImageSrc] = useState('');
  const [croppedAreaPixels, setCroppedAreaPixels] = useState<any>(null);
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [cropping, setCropping] = useState(false);
  const inputFileRef = useRef<HTMLInputElement | null>(null);
  const router = useRouter();
  const [error, setError] = useState('');
  const [uploading, setUploading] = useState(false);

  function onSelectFile(e: React.ChangeEvent<HTMLInputElement> | React.DragEvent<HTMLDivElement>) {
    setError('');
    let file;
    if ('dataTransfer' in e && e.dataTransfer && e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      file = e.dataTransfer.files[0];
    } else if ('target' in e && e.target && (e.target as HTMLInputElement).files && (e.target as HTMLInputElement).files!.length > 0) {
      file = (e.target as HTMLInputElement).files![0];
    }
    if (!file) return;
    if (!['image/jpeg', 'image/png'].includes(file.type)) {
      setError('Only JPEG and PNG images are allowed.');
      return;
    }
    if (file.size > 2 * 1024 * 1024) {
      setError('Image must be less than 2MB.');
      return;
    }
    const reader = new FileReader();
    reader.addEventListener('load', () => {
      setImageSrc(reader.result as string);
      setCropping(true);
    });
    reader.readAsDataURL(file);
  }

  function onCropComplete(_: any, croppedAreaPixels: any) {
    setCroppedAreaPixels(croppedAreaPixels);
  }

  async function handleCropAndUpload() {
    if (!imageSrc || !croppedAreaPixels) return;
    setUploading(true);
    setError('');
    try {
      console.log('Starting image crop and upload...');
      const croppedBlob = await getCroppedImg(imageSrc, croppedAreaPixels, zoom, 1) as Blob;
      console.log('Image cropped successfully, blob size:', croppedBlob.size);
      
      const storage = getStorage();
      const storageRef = ref(storage, `merchandise-images/${Date.now()}.jpg`);
      console.log('Uploading to Firebase Storage...');
      
      await uploadBytes(storageRef, croppedBlob);
      console.log('Upload completed, getting download URL...');
      
      const url = await getDownloadURL(storageRef);
      console.log('Download URL obtained:', url);
      
      setForm(f => ({ ...f, imageUrl: url }));
      setCropping(false);
      setError(''); // Clear any previous errors
    } catch (err) {
      console.error('Error during image upload:', err);
      setError(`Failed to upload image: ${err instanceof Error ? err.message : 'Unknown error'}`);
    } finally {
      setUploading(false);
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!form.name || form.price <= 0) return;

    setSaving(true);
    try {
      await addDoc(collection(db, 'merchandise'), {
        ...form,
        createdAt: Date.now(),
      });
      router.push('/admin/merchandise');
    } catch (error) {
      console.error('Error adding merchandise:', error);
      alert('Error adding merchandise');
    } finally {
      setSaving(false);
    }
  };

  return (
    <div>
      <AdminHeader />
      <main className="container mx-auto px-4 py-12 max-w-xl">
        <h1 className="text-3xl font-headline font-bold text-primary mb-8 text-center">Add Merchandise</h1>
        
        <form onSubmit={handleSubmit} className="bg-card rounded-lg shadow-md p-6 space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">Name</label>
            <input
              className="w-full p-2 border rounded"
              value={form.name}
              onChange={(e) => setForm({ ...form, name: e.target.value })}
              placeholder="Merchandise name"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-1">Description</label>
            <textarea
              className="w-full p-2 border rounded"
              value={form.description}
              onChange={(e) => setForm({ ...form, description: e.target.value })}
              placeholder="Description"
              rows={3}
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium mb-1">Price</label>
            <input
              className="w-full p-2 border rounded"
              type="number"
              step="0.01"
              min="0"
              value={form.price}
              onChange={(e) => setForm({ ...form, price: parseFloat(e.target.value) || 0 })}
              placeholder="0.00"
              required
            />
          </div>

          <div className="flex flex-col gap-2">
            <span className="font-semibold">Merchandise Image</span>
            <div className="flex gap-4 items-center">
              <input
                type="file"
                accept="image/jpeg,image/png"
                ref={inputFileRef}
                onChange={onSelectFile}
                className="hidden"
              />
              <button
                type="button"
                className="bg-primary text-primary-foreground px-4 py-2 rounded font-semibold"
                onClick={() => inputFileRef.current?.click()}
              >
                <PhotoCamera className="inline mr-2" /> Upload Image
              </button>
              {form.imageUrl && (
                <img src={form.imageUrl} alt="Merchandise" className="h-16 w-16 object-cover rounded" />
              )}
            </div>
            {error && <p className="text-destructive text-sm mt-2">{error}</p>}
            {cropping && (
              <div className="relative w-full h-64 bg-muted rounded mt-4">
                <Cropper
                  image={imageSrc}
                  crop={crop}
                  zoom={zoom}
                  aspect={1}
                  onCropChange={setCrop}
                  onZoomChange={setZoom}
                  onCropComplete={onCropComplete}
                />
                <div className="absolute bottom-4 left-0 right-0 flex flex-col items-center">
                  <Slider
                    min={1}
                    max={3}
                    step={0.01}
                    value={zoom}
                    onChange={(_, v) => setZoom(v as number)}
                    className="w-1/2"
                  />
                  <button
                    type="button"
                    className="mt-2 bg-primary text-primary-foreground px-4 py-2 rounded font-semibold"
                    onClick={handleCropAndUpload}
                    disabled={uploading}
                  >
                    {uploading ? <CircularProgress size={20} /> : 'Crop & Upload'}
                  </button>
                </div>
              </div>
            )}
          </div>
          
          <div className="flex items-center gap-2">
            <input
              type="checkbox"
              checked={form.enabled}
              onChange={(e) => setForm({ ...form, enabled: e.target.checked })}
              id="enabled"
            />
            <label htmlFor="enabled">Enabled</label>
          </div>
          
          <div className="flex gap-4 pt-4">
            <button
              type="submit"
              className="bg-primary text-primary-foreground px-4 py-2 rounded font-semibold flex-1 hover:bg-primary/90 disabled:opacity-50"
              disabled={saving}
            >
              {saving ? 'Saving...' : 'Add Merchandise'}
            </button>
            <Link
              href="/admin/merchandise"
              className="bg-gray-500 text-white px-4 py-2 rounded font-semibold flex-1 text-center hover:bg-gray-600"
            >
              Cancel
            </Link>
          </div>
        </form>
      </main>
    </div>
  );
} 